import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import UserService from '../services/UserService';
import planeBG from "../assets/images/planebg1.jpg";
import Footer from './Footer';
import Header from './Header';

/** 
 * 
 * This component will render Register page for the app 
 * UserService: Service for registering new user
*/
export default class Register extends Component {

	constructor(props) {
		super(props);
		this.service = new UserService();
		this.state = {
			userId: 0,
			fname: "",
			email: "",
			phone: "",
			username: "",
			password: "",
			isadmin: 0,
			cp: "",
			flag: false
		}
	}

	componentDidMount() {

	}

	handleInput = (event) => {
		const name = event.target.name;
		const value = event.target.value;
		this.setState({
			[name]: value
		});
	}

	/** this method is for password confirmation */
	handlePass = (event) => {
		if (event.target.value !== this.state.password) {
			this.setState({ cp: "Invalid Password!!" });
			this.setState({ flag: false });
		}
		else {
			this.setState({ cp: "" });
			this.setState({ flag: true });
		}
	}

	/** 
	 * this method interacts with service to register new user
	 * redirects to login page
	*/
	registerUser = (e) => {
		e.preventDefault();
		if (!e.target.closest("form").reportValidity()) {
			return;
		}

		this.service.addUser(this.state).then(response => {

			if (response.status === 200) {
				console.log("Success:", response.data);
				this.setState({
					userId: 0,
					fname: "",
					email: "",
					phone: 0,
					username: "",
					password: "",
					isadmin: 0
				});
				this.props.history.push('/login/');
			}
		}).catch(error => {
			console.error("Registration Error:", error);
			alert('Registration failed. Check browser console for details.');
		});
	}

	render() {

		return (
			<div class='pt-5'>
				<Header />
				<div class="py-5" style={{ backgroundImage: `url(${planeBG})`, backgroundSize: 'cover' }}>
					<div className="row mb-4">
						<div className="col-lg-8 mx-auto text-center">
							<h1 className="display-6" style={{ color: 'white', fontWeight: '50pt' }}>Registration</h1>
						</div>
					</div>
					<div className="row">
						<div className="col-md-6 mx-auto">
							<div className="card ">
								<div className="card-header">
									<div className="bg-white shadow-sm pt-4 pl-2 pr-2 pb-2">

										<div className="tab-content">
											<div className="tab-pane fade show active pt-3">
												<form>
													<div className="form-group">
														<h6><span className="form-label">Name</span></h6>
														<input type="text" name="fname" value={this.state.fname} onChange={this.handleInput} required pattern="[A-Za-z ]+" title="Name should only contain alphabets and spaces" className="form-control" />
														<small className="text-muted">Mandatory, alphabets and spaces only</small>
													</div>

													<div className="form-group">
														<h6><span className="form-label">Email</span></h6>
														<input type="email" name="email" value={this.state.email} onChange={this.handleInput} required className="form-control" />
														<small className="text-muted">Mandatory, valid email format</small>
													</div>
													<div className="form-group">
														<h6><span className="form-label">Contact</span></h6>
														<input name="phone" type="text" pattern="[0-9]{10}" maxLength="10" title="Contact number should be exactly 10 digits" value={this.state.phone} onChange={this.handleInput} required className="form-control" />
														<small className="text-muted">Mandatory, exactly 10 digits</small>
													</div>

													<div className="form-group">
														<h6><span className="form-label">Username</span></h6>
														<input type="text" name="username" value={this.state.username} onChange={this.handleInput} required className="form-control" />
													</div>
													<div className="form-group">
														<h6><span className="form-label">Password</span></h6>
														<input
															type="password"
															name="password"
															value={this.state.password}
															onChange={this.handleInput}
															required
															minLength="6"
															className="form-control"
														/>
														<small className="text-muted">Mandatory, minimum 6 characters</small>
													</div>

													<div className="form-group">
														<h6><span className="form-label">Confirm Password</span></h6>
														<input type="password" name="cpasswd" onChange={this.handlePass} className="form-control" required />
														<div className="text-danger">{this.state.cp} </div>
													</div>

													<div className="card-footer">
														<button onClick={this.registerUser} className="subscribe btn btn-primary btn-block shadow-sm" disabled={!this.state.flag}>Register</button>

													</div>
												</form>

											</div>
										</div>
										<div className="form-group" >

											<div ><Link className="card-link" to="/login"><button type="button" className="btn  btn-link btn-block">Already registered? Login Now!</button></Link>  </div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<Footer />
			</div>

		);
	}
}